'use client';
import { fetchVideos } from '@/features/videos/video.server';
import { VideoListResponse } from '@/features/videos/video.type';
import VideoListCard from '@/features/videos/components/VideoListCard';

export default async function VideosPage() {
  const videos: VideoListResponse[] = await fetchVideos();

  return (
    <div className="max-w-7xl mx-auto py-12">
      <h1 className="text-3xl font-medium text-center mb-4 text-gray-700">みんなの学習メモ</h1>
      <p className="text-center text-sm text-gray-500 mb-12">
        YouTubeを見て学んだことを、気軽に残しています
      </p>

      <div className="text-center text-xl">
        <button className="mr-3 hover:bg-gray-500">動画ごと</button>
        <button className="mr-3 hover:bg-red-500">投稿ごと</button>
      </div>

      <ul className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-3">
        {videos.map((video) => (
          <li key={video.id}>
            <VideoListCard video={video} />
          </li>
        ))}
      </ul>
    </div>
  );
}
